 body,
 html {
     background: #111;
     color: white;
     font-family: sans-serif;
     text-align: center;
     text-transform: uppercase;
 }

 body,
 html,
 #screen {
     height: 100vh;
     width: 100%;
 }

 #screen {
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
 }

 .grid {
     display: flex;
     justify-content: center;
     flex-direction: column;
     gap: 8px;
 }

 .row {
     display: flex;
     gap: 8px;
 }

